








































































import loadMore from '../../components/load-more.vue'
export default {
	data(){
		return{
			type:0,
			statusTitle:'',
			list: [],
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	methods:{
		selectTab(e){
			this.type = e.currentTarget.dataset.type;
			console.log(this.type);
			uni.showNavigationBarLoading() //在标题栏中显示加载
			//模拟加载
			setTimeout(() => {
				uni.hideNavigationBarLoading() //完成停止加载
			}, 1000);
			this.getData()
		},
		// 路由跳转传参
		goPay(){
			if(this.type==1){
				uni.navigateTo({
					url:'orderDetail?type='+this.type
				})
			}else if(this.type==2){
				uni.showToast({
					title: '已提醒商家发货。!',
					duration:1000,
					icon:'none'
				});  
			}else if(this.type==3){
				uni.navigateTo({
					url:'orderDetail?type='+this.type
				})
			}else if(this.type==4){
				uni.navigateTo({
					url:'orderDetail?type='+this.type
				})
			}
		},
		getData(){
			if(this.type==1){
				this.statusTitle='等待交易完成'
			}else if(this.type==2){
				this.statusTitle='付款已完成'
			}else if(this.type==3){
				this.statusTitle='商家已发货'
			}else if(this.type==4){
				this.statusTitle='订单已完成'
			}
		}
	},
	// 获取页面的参数
	onLoad() {
		let list = [];
		for (let i = 1; i < 11; i++) {
			list.push(i);
		}
		this.list = list;
	},
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		let list = [],
			maxItem = this.list[this.list.length - 1],
			length = maxItem + 6;
		for (let i = maxItem + 1; i < length; i++) {
			list.push(i);
		}
		setTimeout(() => {
			if (length > 26) {
				this.loadingType = 2;
				return;
			}
			this.list = this.list.concat(list);
			this.loadingType = 0;
		}, 1500);
	},
	components: {
		loadMore
	}
}



